export default {
	data() {
		return {
			alreadyLogin: false,
			isWX: false,
			userInfo: null,
			userInfoServer: null,
			phone: "",
			code: "",
			text: "发送验证码",
			bindP: false
		}
	},
	onShow: function() {
		if (getApp().getCode()) {
			this.alreadyLogin = true
		} else {
			this.alreadyLogin = false
		}
		uni.onSocketMessage((res) => {
			this.getMessage(JSON.parse(res.data))
		})
		this.isWX = uni.getSystemInfoSync().platform === 'devtools'
	},
	methods: {
		getMessage: function(data) {
			uni.hideLoading()
			let msg = data.msg
			let obj = data.obj
			if (msg == "loginSuccess") {
				getApp().setCode(obj.loginCode)
				this.alreadyLogin = true
				uni.showToast({
					icon: 'success',
					title: '登陆成功'
				})
			} else if (msg == "loginError") {
				this.alreadyLogin = false
				getApp().delCode()
				uni.showToast({
					icon: 'none',
					title: '登陆失败'
				})
			} else if (msg == "vcodeError") {
				this.alreadyLogin = false
				getApp().delCode()
				uni.showToast({
					icon: 'none',
					title: '验证码错误'
				})
			} else if (msg == "bindPhone") {
				getApp().globalData.openid = obj.openid
				this.isWX = false
				this.bindP = true
				uni.showToast({
					icon: 'none',
					title: '请绑定手机'
				})
			} else if (msg == "bindPhoneSuccess") {
				getApp().sendSocket("login", {
					"code": getApp().globalData.openid
				})
				uni.showToast({
					icon: 'success',
					title: '绑定成功'
				})
			} else if (msg == "vcode") {
				this.code = obj.vcode
			}
		},
		barTo: function(e) {
			let name = e.currentTarget.dataset.name
			if (name == "scan") {
				getApp().scan()
			} else {
				getApp().barTo(name)
			}
		},
		login: function() {
			let phone = this.phone
			let code = this.code
			if (this.bindP) {
				getApp().sendSocket("bindPhone", {
					"phone": phone,
					"vcode": code,
					"openid": getApp().globalData.openid
				})
			} else {
				getApp().sendSocket("login", {
					"code": phone,
					"vcode": code
				})
			}
		},
		setPhone: function(e) {
			this.phone = e.detail.value
		},
		setCode: function(e) {
			this.code = e.detail.value
		},
		sendVcode: function(e) {
			if (this.text == "发送验证码") {
				getApp().sendSocket("sendVcode", {
					phone: this.phone
				})
				var s = 60
				this.text = `重新发送(${s})`
				var reSendTip = setInterval(() => {
					s = s - 1
					this.text = `重新发送(${s})`
					if (s <= 0) {
						clearInterval(reSendTip)
						this.text = "发送验证码"
					}
				}, 1000)
			}
		},
		oauth() {
			uni.login({
				provider: 'weixin',
				success: (res) => {
					let code = res.code
					getApp().sendSocket("login", {
						"code": code
					})
					uni.getUserInfo({
						provider: 'weixin',
						success: (infoRes) => {
							// console.log(infoRes)
							// let code = infoRes.code
							// getApp().sendSocket("login", {
							// 	"code": code
							// })
						},
						fail() {
							uni.showToast({
								icon: 'none',
								title: '登陆失败'
							})
						}
					});
				},
				fail: (err) => {
					console.error('授权登录失败：' + JSON.stringify(err));
				}
			});
		},
		getUserInfo({
			detail
		}) {
			if (detail.userInfo) {
				this.userInfo = detail.userInfo
				getApp().globalData.userInfo = detail.userInfo
			} else {
				uni.showToast({
					icon: 'none',
					title: '登陆失败'
				});
			}
		},
		logout: function() {
			getApp().sendSocket("logout")
			this.alreadyLogin = false
		}
	},

}
